'use client'
import { useEffect, useState, useRef } from "react"
import Loading from "@/components/Loading"
import { useAuth } from "@clerk/nextjs"
import axios from "axios"
import toast from "react-hot-toast"
import { Eye, Printer, FileText, Check, X } from "lucide-react"

export default function StoreOrders() {
    const [orders, setOrders] = useState([])
    const [loading, setLoading] = useState(true)
    const [selectedOrder, setSelectedOrder] = useState(null)
    const [isModalOpen, setIsModalOpen] = useState(false)
    const [rejectReason, setRejectReason] = useState("")
    const [showRejectModal, setShowRejectModal] = useState(null)
    const [filter, setFilter] = useState('all')
    const audioRef = useRef(null)
    const prevOrderCount = useRef(0)

    const { getToken } = useAuth()
    const currency = process.env.NEXT_PUBLIC_CURRENCY_SYMBOL || '$'

    useEffect(() => {
        // Initialize notification sound
        audioRef.current = new Audio('/notification.mp3') // Ensure you have this file in public folder
    }, [])

    const fetchOrders = async (silent = false) => {
       try {
        const token = await getToken()
        const { data } = await axios.get('/api/store/orders', {headers: { Authorization: `Bearer ${token}` }})
        
        // Play sound if new order arrives
        if(data.orders.length > prevOrderCount.current && prevOrderCount.current !== 0){
            audioRef.current?.play().catch(e => console.log('Audio play failed'))
            toast.success("New Order Received!")
        }
        prevOrderCount.current = data.orders.length
        setOrders(data.orders)
       } catch (error) {
        if(!silent) toast.error(error?.response?.data?.error || error.message)
       } finally {
        if(!silent) setLoading(false)
       }
    }

    // Polling for new orders every 30 seconds
    useEffect(() => {
        fetchOrders()
        const interval = setInterval(() => fetchOrders(true), 30000)
        return () => clearInterval(interval)
    }, [])

    const updateOrderStatus = async (orderId, status, reason = null) => {
        try {
            const token = await getToken()
            await axios.post('/api/store/orders',{ orderId, status, reason }, {headers: { Authorization: `Bearer ${token}` }})
            
            setOrders(prev => prev.map(order => 
                order.id === orderId ? {...order, status, rejectionReason: reason} : order
            ))
            
            toast.success(`Order ${status.toLowerCase().replace(/_/g, ' ')}`)
            if(status === 'REJECTED') {
                setShowRejectModal(null)
                setRejectReason("")
                setIsModalOpen(false)
            }
       } catch (error) {
            toast.error(error?.response?.data?.error || error.message)
       }
    }

    const printInvoice = () => {
        const printContent = document.getElementById('invoice-section').innerHTML
        const originalContent = document.body.innerHTML
        document.body.innerHTML = printContent
        window.print()
        document.body.innerHTML = originalContent
        window.location.reload() 
    }

    const filteredOrders = filter === 'all' ? orders : orders.filter(o => o.status === filter)

    if (loading) return <Loading />

    return (
        <div className="mb-20">
            <div className="flex flex-col md:flex-row justify-between items-center mb-6 gap-4">
                <h1 className="text-2xl text-slate-500">Store <span className="text-slate-800 font-medium">Orders</span></h1>
                <div className="flex gap-2 overflow-x-auto pb-2">
                    {['all', 'ORDER_PLACED', 'PROCESSING', 'READY_FOR_PICKUP', 'DELIVERED'].map((status) => (
                        <button 
                            key={status}
                            onClick={() => setFilter(status)}
                            className={`px-4 py-2 rounded text-xs md:text-sm capitalize whitespace-nowrap transition-colors ${filter === status ? 'bg-slate-800 text-white' : 'bg-slate-100 text-slate-600 hover:bg-slate-200'}`}
                        >
                            {status.replace(/_/g, ' ')}
                        </button>
                    ))}
                </div>
            </div>

            <div className="overflow-x-auto rounded-md shadow border border-gray-200">
                <table className="w-full text-sm text-left text-gray-600">
                    <thead className="bg-gray-50 text-gray-700 text-xs uppercase tracking-wider">
                        <tr>
                            <th className="px-4 py-3">Order ID</th>
                            <th className="px-4 py-3">Customer</th>
                            <th className="px-4 py-3">Total</th>
                            <th className="px-4 py-3">Status</th>
                            <th className="px-4 py-3">Date</th>
                            <th className="px-4 py-3">Actions</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-100">
                        {filteredOrders.map((order) => (
                            <tr key={order.id} className="hover:bg-gray-50">
                                <td className="px-4 py-3 font-medium">#{order.id.slice(-6)}</td>
                                <td className="px-4 py-3">
                                    <div>{order.user?.name}</div>
                                    <div className="text-xs text-gray-400">{order.address?.phone}</div>
                                </td>
                                <td className="px-4 py-3">{currency}{order.total}</td>
                                <td className="px-4 py-3">
                                    <span className={`px-2 py-1 rounded-full text-xs font-medium
                                        ${order.status === 'ORDER_PLACED' ? 'bg-blue-100 text-blue-800' : ''}
                                        ${order.status === 'PROCESSING' ? 'bg-yellow-100 text-yellow-800' : ''}
                                        ${order.status === 'DELIVERED' ? 'bg-green-100 text-green-800' : ''}
                                        ${order.status === 'REJECTED' ? 'bg-red-100 text-red-800' : ''}
                                        ${['READY_FOR_PICKUP', 'OUT_FOR_DELIVERY'].includes(order.status) ? 'bg-purple-100 text-purple-800' : ''}
                                    `}>
                                        {order.status.replace(/_/g, ' ')}
                                    </span>
                                </td>
                                <td className="px-4 py-3">{new Date(order.createdAt).toLocaleDateString()}</td>
                                <td className="px-4 py-3">
                                    <button onClick={() => { setSelectedOrder(order); setIsModalOpen(true) }} className="p-2 bg-slate-100 text-slate-600 rounded hover:bg-slate-200" title="View Details">
                                        <Eye size={16} />
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {/* Order Details Modal */}
            {isModalOpen && selectedOrder && (
                <div className="fixed inset-0 flex items-center justify-center bg-black/50 backdrop-blur-sm z-50 p-4" onClick={() => setIsModalOpen(false)}>
                    <div className="bg-white rounded-lg shadow-xl w-full max-w-3xl max-h-[90vh] overflow-y-auto" onClick={e => e.stopPropagation()}>
                        
                        {/* Printable Invoice Area */}
                        <div id="invoice-section" className="p-8 bg-white">
                            <div className="flex justify-between border-b pb-6 mb-6">
                                <div>
                                    <h2 className="text-2xl font-bold text-slate-800">INVOICE</h2>
                                    <p className="text-sm text-gray-500 mt-1">Order #{selectedOrder.id}</p>
                                </div>
                                <div className="text-right">
                                    <h3 className="font-semibold text-lg text-slate-700">Flour2Door</h3>
                                    <p className="text-sm text-gray-500">{new Date(selectedOrder.createdAt).toLocaleDateString()}</p>
                                </div>
                            </div>
                            
                            <div className="grid md:grid-cols-2 gap-8 mb-8">
                                <div>
                                    <h4 className="font-semibold mb-3 text-xs text-gray-400 uppercase tracking-wide">Bill To</h4>
                                    <p className="font-medium text-slate-800">{selectedOrder.user?.name}</p>
                                    <p className="text-sm text-gray-600">{selectedOrder.address?.phone}</p>
                                    <p className="text-sm text-gray-600 mt-1">{selectedOrder.address?.street}, {selectedOrder.address?.city}</p>
                                    <p className="text-sm text-gray-600">{selectedOrder.address?.state} - {selectedOrder.address?.zip}</p>
                                </div>
                                <div>
                                    <h4 className="font-semibold mb-3 text-xs text-gray-400 uppercase tracking-wide">Payment Details</h4>
                                    <div className="flex justify-between text-sm mb-2">
                                        <span className="text-gray-600">Method:</span>
                                        <span className="font-medium text-slate-800">{selectedOrder.paymentMethod}</span>
                                    </div>
                                    <div className="flex justify-between text-sm">
                                        <span className="text-gray-600">Status:</span>
                                        <span className={`font-medium ${selectedOrder.isPaid ? 'text-green-600' : 'text-orange-600'}`}>
                                            {selectedOrder.isPaid ? 'Paid' : 'Pending'}
                                        </span>
                                    </div>
                                </div>
                            </div>

                            <table className="w-full mb-8 text-sm">
                                <thead className="bg-slate-50 border-y border-slate-200">
                                    <tr>
                                        <th className="p-3 text-left font-semibold text-slate-600">Item</th>
                                        <th className="p-3 text-center font-semibold text-slate-600">Qty</th>
                                        <th className="p-3 text-right font-semibold text-slate-600">Price</th>
                                        <th className="p-3 text-right font-semibold text-slate-600">Total</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {selectedOrder.orderItems.map((item, i) => (
                                        <tr key={i} className="border-b border-slate-100">
                                            <td className="p-3 font-medium text-slate-700">{item.product?.name}</td>
                                            <td className="p-3 text-center text-slate-600">{item.quantity}</td>
                                            <td className="p-3 text-right text-slate-600">{currency}{item.price}</td>
                                            <td className="p-3 text-right font-medium text-slate-800">{currency}{item.price * item.quantity}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>

                            <div className="flex justify-end">
                                <div className="w-64 space-y-2">
                                    <div className="flex justify-between text-sm text-slate-600">
                                        <span>Subtotal:</span>
                                        <span>{currency}{selectedOrder.total}</span>
                                    </div>
                                    {/* Discount logic can be added here if saved in DB */}
                                    <div className="flex justify-between font-bold text-xl text-slate-800 border-t pt-3 mt-2">
                                        <span>Total:</span>
                                        <span>{currency}{selectedOrder.total}</span>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Workflow Action Buttons */}
                        <div className="bg-gray-50 p-6 border-t flex flex-wrap gap-3 justify-end items-center">
                            {selectedOrder.status === 'ORDER_PLACED' && (
                                <>
                                    <button 
                                        onClick={() => updateOrderStatus(selectedOrder.id, 'PROCESSING')} 
                                        className="bg-green-600 text-white px-5 py-2 rounded-lg hover:bg-green-700 flex items-center gap-2 shadow-sm transition-all"
                                    >
                                        <Check size={18}/> Accept Order
                                    </button>
                                    <button 
                                        onClick={() => setShowRejectModal(selectedOrder.id)} 
                                        className="bg-red-500 text-white px-5 py-2 rounded-lg hover:bg-red-600 flex items-center gap-2 shadow-sm transition-all"
                                    >
                                        <X size={18}/> Reject
                                    </button>
                                </>
                            )}
                            
                            {selectedOrder.status === 'PROCESSING' && (
                                <button 
                                    onClick={() => updateOrderStatus(selectedOrder.id, 'READY_FOR_PICKUP')} 
                                    className="bg-blue-600 text-white px-5 py-2 rounded-lg hover:bg-blue-700 shadow-sm transition-all"
                                >
                                    Mark Ready for Pickup
                                </button>
                            )}

                            {selectedOrder.status !== 'REJECTED' && (
                                <button onClick={printInvoice} className="border border-slate-300 bg-white text-slate-700 px-5 py-2 rounded-lg hover:bg-slate-50 flex items-center gap-2 transition-all">
                                    <Printer size={18}/> Print Invoice
                                </button>
                            )}
                            
                            <button onClick={() => setIsModalOpen(false)} className="px-5 py-2 text-slate-600 hover:text-slate-800 font-medium">Close</button>
                        </div>
                    </div>
                </div>
            )}

            {/* Reject Reason Modal */}
            {showRejectModal && (
                <div className="fixed inset-0 flex items-center justify-center bg-black/50 backdrop-blur-sm z-50 px-4">
                    <div className="bg-white rounded-lg p-6 w-full max-w-md shadow-2xl">
                        <h3 className="text-xl font-bold mb-2 text-slate-800">Reject Order</h3>
                        <p className="text-sm text-slate-500 mb-4">Please provide a reason. This will be sent to the customer and a refund will be initiated.</p>
                        <textarea 
                            className="w-full border border-slate-300 rounded-lg p-3 mb-4 focus:ring-2 focus:ring-red-500 outline-none" 
                            rows="4" 
                            placeholder="e.g. Item out of stock..."
                            value={rejectReason}
                            onChange={(e) => setRejectReason(e.target.value)}
                        />
                        <div className="flex justify-end gap-3">
                            <button onClick={() => { setShowRejectModal(null); setRejectReason("") }} className="px-4 py-2 text-slate-600 hover:bg-slate-100 rounded-lg">Cancel</button>
                            <button 
                                onClick={() => updateOrderStatus(showRejectModal, 'REJECTED', rejectReason)} 
                                disabled={!rejectReason.trim()}
                                className="bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700 disabled:opacity-50 disabled:cursor-not-allowed shadow-sm"
                            >
                                Confirm Rejection
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    )
}